'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { useParams, useRouter } from 'next/navigation';
import Link from 'next/link';
import { toast } from 'sonner';
import { 
    ArrowLeft, 
    Calendar, 
    User, 
    CreditCard, 
    Trash2, 
    Printer, 
    Store,
    FileText
} from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge'; 
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';

// Types
import { Pembelian } from '@/types';

export default function DetailPembelianPage() {
    const params = useParams();
    const router = useRouter();
    const id = params.id;

    const [data, setData] = useState<Pembelian | null>(null);
    const [loading, setLoading] = useState(true);

    // 1. Fetch Data Detail
    const fetchDetail = async () => {
        try {
            const response = await axios.get(`/api/pembelians/${id}`);
            setData(response.data);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat detail pembelian.");
            router.push('/admin/bahan-baku/pembelian'); // Redirect jika error
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        if (id) fetchDetail();
    }, [id]);

    // Helper Format
    const formatRupiah = (num: number | string) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));
    
    const formatDate = (dateString: string) => 
        new Date(dateString).toLocaleDateString('id-ID', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric' });

    // Handle Void (Langsung di halaman detail)
    const handleVoid = async () => {
        if(!confirm("Yakin batalkan transaksi ini? Stok akan dikurangi otomatis.")) return;
        try {
            await axios.delete(`/api/pembelians/${id}`);
            toast.success("Pembelian dibatalkan.");
            fetchDetail(); // Refresh data
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal membatalkan.");
        }
    };

    if (loading) return <div className="p-8 text-center">Memuat detail...</div>;
    if (!data) return <div className="p-8 text-center">Data tidak ditemukan.</div>;

    return (
        <div className="space-y-6 max-w-5xl mx-auto pb-20">
            <div className="flex items-center justify-between print:hidden">
                <div className="flex items-center gap-4">
                    <Link href="/admin/bahan-baku/pembelian">
                        <Button variant="outline" size="icon">
                            <ArrowLeft className="h-4 w-4" />
                        </Button>
                    </Link>
                    <div>
                        <h1 className="text-2xl font-bold text-gray-900">Detail Pembelian</h1>
                        <div className="flex items-center gap-2 text-sm text-gray-500">
                            <span className="font-mono">{data.kode_pembelian}</span>
                            <span>â€¢</span>
                            {data.status === 'selesai' ? (
                                <Badge className="bg-green-100 text-green-800 hover:bg-green-100 border-green-200">Selesai</Badge>
                            ) : (
                                <Badge className="destructive text-white bg-red-500">Dibatalkan</Badge>
                            )}
                        </div>
                    </div>
                </div>
                <div className="flex gap-2">
                    <Button variant="outline" onClick={() => window.print()}>
                        <Printer className="w-4 h-4 mr-2" /> Cetak
                    </Button>
                    {data.status === 'selesai' && (
                        <Button variant="destructive" onClick={handleVoid}>
                            <Trash2 className="w-4 h-4 mr-2" /> Batalkan
                        </Button>
                    )}
                </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                
                <Card className="md:col-span-2 h-fit">
                    <CardHeader className="bg-gray-50 border-b">
                        <CardTitle className="text-base flex items-center gap-2">
                            <Store className="w-4 h-4" /> Rincian Barang
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="p-0">
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Barang</TableHead>
                                    <TableHead className="text-right">Jml</TableHead>
                                    <TableHead className="text-right">Harga Satuan</TableHead>
                                    <TableHead className="text-right">Total</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {data.details?.map((item) => (
                                    <TableRow key={item.id}>
                                        <TableCell>
                                            <div className="font-medium">{item.bahan_baku?.nama_bahan_baku}</div>
                                            <div className="text-xs text-gray-500">{item.bahan_baku?.kategori?.nama_kategori}</div>
                                        </TableCell>
                                        <TableCell className="text-right">
                                            {Number(item.jumlah)} {item.bahan_baku?.satuan?.nama_satuan}
                                        </TableCell>
                                        <TableCell className="text-right font-mono text-gray-600">
                                            {formatRupiah(item.harga_beli_satuan)}
                                        </TableCell>
                                        <TableCell className="text-right font-bold text-gray-900">
                                            {formatRupiah(item.subtotal)}
                                        </TableCell>
                                    </TableRow>
                                ))}
                                {/* Baris Total */}
                                <TableRow className="bg-gray-50 font-bold">
                                    <TableCell colSpan={3} className="text-right">Total Faktur</TableCell>
                                    <TableCell className="text-right text-lg text-[#D93F21]">
                                        {formatRupiah(data.total_harga_faktur)}
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </CardContent>
                </Card>

                <div className="space-y-6">
                    <Card>
                        <CardHeader className="pb-3">
                            <CardTitle className="text-sm text-gray-500 uppercase tracking-wider">Supplier</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="font-bold text-lg">{data.supplier?.nama_supplier}</div>
                            <div className="text-sm text-gray-600 mt-1">{data.supplier?.alamat || '-'}</div>
                            <div className="text-sm text-gray-600 mt-1">Telp: {data.supplier?.nomor_telepon}</div>
                            <div className="text-sm text-gray-600">CP: {data.supplier?.kontak_person}</div>
                        </CardContent>
                    </Card>

                    <Card>
                        <CardHeader className="pb-3">
                            <CardTitle className="text-sm text-gray-500 uppercase tracking-wider">Info Transaksi</CardTitle>
                        </CardHeader>
                        <CardContent className="space-y-4">
                            <div className="flex items-center gap-3">
                                <div className="w-8 h-8 rounded-full bg-blue-50 flex items-center justify-center text-blue-600">
                                    <Calendar className="w-4 h-4" />
                                </div>
                                <div>
                                    <p className="text-xs text-gray-500">Tanggal Pembelian</p>
                                    <p className="font-medium text-sm">{formatDate(data.tanggal_pembelian)}</p>
                                </div>
                            </div>
                            
                            <div className="flex items-center gap-3">
                                <div className="w-8 h-8 rounded-full bg-purple-50 flex items-center justify-center text-purple-600">
                                    <FileText className="w-4 h-4" />
                                </div>
                                <div>
                                    <p className="text-xs text-gray-500">No. Faktur Supplier</p>
                                    <p className="font-medium text-sm">{data.no_faktur_supplier || '-'}</p>
                                </div>
                            </div>

                            <Separator />

                            <div className="flex items-center gap-3">
                                <div className="w-8 h-8 rounded-full bg-green-50 flex items-center justify-center text-green-600">
                                    <CreditCard className="w-4 h-4" />
                                </div>
                                <div>
                                    <p className="text-xs text-gray-500">Metode Pembayaran</p>
                                    <p className="font-medium text-sm">{data.payment_method?.nama_metode}</p>
                                </div>
                            </div>

                            <div className="flex items-center gap-3">
                                <div className="w-8 h-8 rounded-full bg-orange-50 flex items-center justify-center text-orange-600">
                                    <User className="w-4 h-4" />
                                </div>
                                <div>
                                    <p className="text-xs text-gray-500">Dicatat Oleh (Admin)</p>
                                    <p className="font-medium text-sm">{data.user?.name}</p>
                                </div>
                            </div>
                        </CardContent>
                    </Card>

                    {/* Catatan */}
                    {data.catatan && (
                        <Card>
                            <CardHeader className="pb-3">
                                <CardTitle className="text-sm text-gray-500 uppercase tracking-wider">Catatan</CardTitle>
                            </CardHeader>
                            <CardContent>
                                <p className="text-sm text-gray-600 italic">"{data.catatan}"</p>
                            </CardContent>
                        </Card>
                    )}
                </div>

            </div>
        </div>
    );
}